import { useTranslation } from "react-i18next";
import Modal from "../../../Ui/Modal/Modal";
import style from "../../../UserStaffMember/PlateformUser/PlateformUser.module.css";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import LectureStudentAttendanceSubjectListCard from "./LectureStudentAttendanceSubjectListCard";
import { useEffect, useState } from "react";
import { useStudentStatsLectureWiseOneStudentSubjectList } from "../../../Hooks/ApiHooks/StudentManager/student-manager-api";
const LectureStudentAttendanceSubjectList = ({ onClose, clsId, sid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [subjectList, setSubjectList] = useState([]);
  const {
    studentStatsLectureWiseOneStudentSubjectList,
    studentStatsLectureWiseOneStudentSubjectListLoading,
  } = useStudentStatsLectureWiseOneStudentSubjectList({
    data: {
      cid: clsId,
      sid: sid,
    },
    skip: !sid ? !sid : !clsId,
  });

  useEffect(() => {
    if (studentStatsLectureWiseOneStudentSubjectList?.length > 0) {
      setSubjectList(studentStatsLectureWiseOneStudentSubjectList);
    }
  }, [studentStatsLectureWiseOneStudentSubjectList]);
  const onSearchEvent = (e) => {
    if (e.target.value) {
      if (!studentStatsLectureWiseOneStudentSubjectListLoading) {
        setSearch(e.target.value);
        let searched = getSubjectSearch(
          studentStatsLectureWiseOneStudentSubjectList ?? [],
          e.target.value
        );
        setSubjectList(searched);
      }
    } else {
      setSearch("");
      setSubjectList(studentStatsLectureWiseOneStudentSubjectList);
    }
  };
  return (
    <Modal
      onClose={onClose}
      customOverlayStyle={{
        width: "70%",
      }}
    >
      <div
        className={style.modal_container}
        style={{
          maxWidth: "100%",
          maxHeight: "calc(100dvh - 100px)",
        }}
      >
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("attendance_subject_wise")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.3rem",
            paddingBottom: "1rem",
          }}
        >
          <div className={style.assign_search_container}>
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              style={{
                width: "100%",
              }}
              onChange={onSearchEvent}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
              style={{
                left: "1%",
              }}
            />
          </div>
          {subjectList?.map((subject) => (
            <LectureStudentAttendanceSubjectListCard
              subject={subject}
              key={subject?._id}
            />
          ))}
          {studentStatsLectureWiseOneStudentSubjectListLoading ? (
            search ? (
              <Loading />
            ) : (
              <Loading />
            )
          ) : search ? (
            subjectList?.length === 0 && (
              <EmptyMessage
                title="No subject found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            studentStatsLectureWiseOneStudentSubjectList?.length === 0 && (
              <EmptyMessage
                title="No subject."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default LectureStudentAttendanceSubjectList;

const getSubjectSearch = (list, search) => {
  let data = list?.filter((lt) => {
    if (lt?.subjectName?.toLowerCase()?.includes(search?.toLowerCase())) {
      return lt;
    } else {
      return null;
    }
  });
  return data;
};
