import { useTranslation } from "react-i18next";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../InstituteAnalytic.module.css";
import { useState } from "react";
import LectureStudentAttendanceSubjectList from "./LectureStudentAttendanceSubjectList";

const LectureStudentAttendanceCard = ({ student, clsId }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");

  const onClose = () => {
    setStep("");
  };
  return (
    <>
      <div
        className={style.staclc_card}
        style={{
          justifyContent: "space-between",
        }}
      >
        <div className={style.staclc_card_container}>
          <img
            src={
              student?.studentProfilePhoto
                ? `${imageShowUrl}/${student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
          />
          <div className={style.staclc_card_inner}>
            <h6>
              {`${student?.studentFirstName ?? ""} ${
                student?.studentMiddleName ?? ""
              } ${student?.studentLastName ?? ""}`}
            </h6>
            <p>
              {t("gr_number")} {student?.studentGRNO ?? ""}
            </p>
          </div>
        </div>
        <div className={style.stcs_stats} onClick={() => setStep(1)}>
          <div>
            {student?.presentLecture ?? 0} / {student?.totalLecture ?? 0}
          </div>
          <div>{student?.avg_attendance ?? 0} %</div>
        </div>
      </div>
      {step === 1 ? (
        <LectureStudentAttendanceSubjectList
          onClose={onClose}
          clsId={clsId}
          sid={student?._id}
        />
      ) : null}
    </>
  );
};

export default LectureStudentAttendanceCard;
