import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import LectureStudentAttendanceCard from "./LectureStudentAttendanceCard";
import { useInView } from "react-intersection-observer";
import { useCallback, useEffect, useState } from "react";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { useStudentStatsLectureWiseStudentList } from "../../../Hooks/ApiHooks/StudentManager/student-manager-api";
import { useLocation } from "react-router-dom";

const LectureStudentAttendance = () => {
  const getQuery = useLocation();
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    studentStatsLectureWiseStudentList,
    studentStatsLectureWiseStudentListLoading,
  } = useStudentStatsLectureWiseStudentList({
    data: {
      cid: getQuery.state?.clsId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.clsId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (studentStatsLectureWiseStudentList?.list?.length) {
      let obj = nextPage(studentStatsLectureWiseStudentList?.list?.length);
      setState(() => obj);
    }
  }, [studentStatsLectureWiseStudentList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) {
      if (!studentStatsLectureWiseStudentListLoading)
        onDebounce(e.target.value);
    } else {
      setPage(() => 1);
      setSearch("");
    }
  };
  // console.info("Asfsdg", getQuery.state);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>
              {getQuery.state?.clsName ?? ""} {t("attendance")}
            </h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {studentStatsLectureWiseStudentList?.list?.map((student, index) =>
            studentStatsLectureWiseStudentList?.list?.length === index + 1 ? (
              <div key={student?._id} ref={ref}>
                <LectureStudentAttendanceCard
                  student={student}
                  clsId={getQuery.state?.clsId}
                />
              </div>
            ) : (
              <LectureStudentAttendanceCard
                student={student}
                key={student?._id}
                clsId={getQuery.state?.clsId}
              />
            )
          )}
          {studentStatsLectureWiseStudentListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            studentStatsLectureWiseStudentList?.list?.length === 0 && (
              <EmptyMessage title="No students found related this search." />
            )
          ) : (
            studentStatsLectureWiseStudentList?.list?.length === 0 && (
              <EmptyMessage title="No students." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default LectureStudentAttendance;
